import {
  COMPANIES_SELECT_ALL,
  COMPANIES_UNSELECT_ALL,
  COMPANY_SELECTED,
  COMPANIES_LOAD,
  COMPANIES_LOAD_SUCCESS,
  COMPANIES_LOAD_FAILED,
  COMPANY_LOAD,
  COMPANY_LOAD_SUCCESS,
  COMPANY_LOAD_FAILED,
  COMPANY_ADD,
  COMPANY_ADD_SUCCESS,
  COMPANY_ADD_FAILED,
  COMPANY_UPDATE,
  COMPANY_UPDATE_SUCCESS,
  COMPANY_UPDATE_FAILED,
  COMPANY_DELETE,
  COMPANY_DELETE_SUCCESS,
  COMPANY_DELETE_FAILED,
  COMPANY_DELETE_ALL,
  COMPANY_DELETE_ALL_SUCCESS,
  COMPANY_DELETE_ALL_FAILED
} from '../constants/ActionTypes';

const INIT_STATE = {
  companies: {
    records: [],
    offset: 0,
    limit: 1000,
    totalRecords: 0
  },
  selectedCompany: null,
  selectedCompanyId: null,
  message: null,
  messageType: 'info',
  companyFilter: {
    offset: 0,
    limit: 1000,
    orderBy: 'name',
    filterGroups: null
  }
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case COMPANIES_SELECT_ALL:
      return {
        ...state,
        companies: {
          ...state.companies,
          records: state.companies.records.map(data => {
            data.selected = true;
            return data;
          })
        }
      }
    case COMPANIES_UNSELECT_ALL:
      return {
        ...state,
        companies: {
          ...state.companies,
          records: state.companies.records.map(data => {
            data.selected = false;
            return data;
          })
        }
      }
    case COMPANY_SELECTED:
      return {
        ...state,
        selectedCompany: action.payload,
        selectedCompanyId: action.payload.id
      };
    case COMPANIES_LOAD:
      return {
        ...state,
        companyFilter: action.payload ? action.payload : state.companyFilter
      };
    case COMPANIES_LOAD_SUCCESS:
      return {
        ...state,
        companies: action.payload
      };
    case COMPANIES_LOAD_FAILED:
      return {
        ...state
      };
    case COMPANY_LOAD:
      return {
        ...state
      };
    case COMPANY_LOAD_SUCCESS:
      return {
        ...state
      };
    case COMPANY_LOAD_FAILED:
      return {
        ...state
      };
    case COMPANY_ADD:
      return {
        ...state
      };
    case COMPANY_ADD_SUCCESS:
      return {
        ...state
      };
    case COMPANY_ADD_FAILED:
      return {
        ...state
      };
    case COMPANY_UPDATE:
      return {
        ...state
      };
    case COMPANY_UPDATE_SUCCESS:
      return {
        ...state
      };
    case COMPANY_UPDATE_FAILED:
      return {
        ...state
      };
    case COMPANY_DELETE:
      return {
        ...state
      };
    case COMPANY_DELETE_SUCCESS:
      return {
        ...state,
        message: action.message
      };
    case COMPANY_DELETE_FAILED:
      return {
        ...state
      };
    case COMPANY_DELETE_ALL:
      return {
        ...state
      };
    case COMPANY_DELETE_ALL_SUCCESS:
      return {
        ...state,
        message: action.message
      };
    case COMPANY_DELETE_ALL_FAILED:
      return {
        ...state
      };
    default:
      return state;
  }
}
