import {
  EMPLOYEES_SELECT_ALL,
  EMPLOYEES_UNSELECT_ALL,
  EMPLOYEE_SELECTED,
  EMPLOYEES_LOAD,
  EMPLOYEES_LOAD_SUCCESS,
  EMPLOYEES_LOAD_FAILED,
  EMPLOYEE_LOAD,
  EMPLOYEE_LOAD_SUCCESS,
  EMPLOYEE_LOAD_FAILED,
  EMPLOYEE_ADD,
  EMPLOYEE_ADD_SUCCESS,
  EMPLOYEE_ADD_FAILED,
  EMPLOYEE_UPDATE,
  EMPLOYEE_UPDATE_SUCCESS,
  EMPLOYEE_UPDATE_FAILED,
  EMPLOYEE_DELETE,
  EMPLOYEE_DELETE_SUCCESS,
  EMPLOYEE_DELETE_FAILED,
  EMPLOYEE_DELETE_ALL,
  EMPLOYEE_DELETE_ALL_SUCCESS,
  EMPLOYEE_DELETE_ALL_FAILED
} from '../constants/ActionTypes';

const INIT_STATE = {
  employees: {
    records: [],
    offset: 0,
    limit: 1000,
    totalRecords: 0
  },
  selectedEmployee: null,
  selectedEmployeeId: null,
  message: null,
  messageType: 'info',
  employeeFilter: {
    offset: 0,
    limit: 1000,
    orderBy: 'name',
    filterGroups: null
  }
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case EMPLOYEES_SELECT_ALL:
      return {
        ...state,
        employees: {
          ...state.employees,
          records: state.employees.records.map(data => {
            data.selected = true;
            return data;
          })
        }
      }
    case EMPLOYEES_UNSELECT_ALL:
      return {
        ...state,
        employees: {
          ...state.employees,
          records: state.employees.records.map(data => {
            data.selected = false;
            return data;
          })
        }
      }
    case EMPLOYEE_SELECTED:
      return {
        ...state,
        selectedEmployee: action.payload,
        selectedEmployeeId: action.payload.id
      };
    case EMPLOYEES_LOAD:
      return {
        ...state,
        employeeFilter: action.payload ? action.payload : state.employeeFilter
      };
    case EMPLOYEES_LOAD_SUCCESS:
      return {
        ...state,
        employees: action.payload
      };
    case EMPLOYEES_LOAD_FAILED:
      return {
        ...state
      };
    case EMPLOYEE_LOAD:
      return {
        ...state
      };
    case EMPLOYEE_LOAD_SUCCESS:
      return {
        ...state
      };
    case EMPLOYEE_LOAD_FAILED:
      return {
        ...state
      };
    case EMPLOYEE_ADD:
      return {
        ...state
      };
    case EMPLOYEE_ADD_SUCCESS:
      return {
        ...state
      };
    case EMPLOYEE_ADD_FAILED:
      return {
        ...state
      };
    case EMPLOYEE_UPDATE:
      return {
        ...state
      };
    case EMPLOYEE_UPDATE_SUCCESS:
      return {
        ...state
      };
    case EMPLOYEE_UPDATE_FAILED:
      return {
        ...state
      };
    case EMPLOYEE_DELETE:
      return {
        ...state
      };
    case EMPLOYEE_DELETE_SUCCESS:
      return {
        ...state,
        message: action.message
      };
    case EMPLOYEE_DELETE_FAILED:
      return {
        ...state
      };
    case EMPLOYEE_DELETE_ALL:
      return {
        ...state
      };
    case EMPLOYEE_DELETE_ALL_SUCCESS:
      return {
        ...state,
        message: action.message
      };
    case EMPLOYEE_DELETE_ALL_FAILED:
      return {
        ...state
      };
    default:
      return state;
  }
}
