import {
  SETTINGS_SELECT_ALL,
  SETTINGS_UNSELECT_ALL,
  SETTING_SELECTED,
  SETTINGS_LOAD,
  SETTINGS_LOAD_SUCCESS,
  SETTINGS_LOAD_FAILED,
  SETTING_LOAD,
  SETTING_LOAD_SUCCESS,
  SETTING_LOAD_FAILED,
  SETTING_ADD,
  SETTING_ADD_SUCCESS,
  SETTING_ADD_FAILED,
  SETTING_UPDATE,
  SETTING_UPDATE_SUCCESS,
  SETTING_UPDATE_FAILED,
  SETTING_DELETE,
  SETTING_DELETE_SUCCESS,
  SETTING_DELETE_FAILED,
  SETTING_DELETE_ALL,
  SETTING_DELETE_ALL_SUCCESS,
  SETTING_DELETE_ALL_FAILED
} from '../constants/ActionTypes';

const INIT_STATE = {
  settings: {
    records: [],
    offset: 0,
    limit: 1000,
    totalRecords: 0
  },
  selectedSetting: null,
  selectedSettingId: null,
  message: null,
  messageType: 'info',
  settingFilter: {
    offset: 0,
    limit: 1000,
    orderBy: 'name',
    filterGroups: null
  }
};

export default (state = INIT_STATE, action) => {
  switch (action.type) {
    case SETTINGS_SELECT_ALL:
      return {
        ...state,
        settings: {
          ...state.settings,
          records: state.settings.records.map(data => {
            data.selected = true;
            return data;
          })
        }
      }
    case SETTINGS_UNSELECT_ALL:
      return {
        ...state,
        settings: {
          ...state.settings,
          records: state.settings.records.map(data => {
            data.selected = false;
            return data;
          })
        }
      }
    case SETTING_SELECTED:
      return {
        ...state,
        selectedSetting: action.payload,
        selectedSettingId: action.payload.id
      };
    case SETTINGS_LOAD:
      return {
        ...state,
        settingFilter: action.payload ? action.payload : state.settingFilter
      };
    case SETTINGS_LOAD_SUCCESS:
      return {
        ...state,
        settings: action.payload
      };
    case SETTINGS_LOAD_FAILED:
      return {
        ...state
      };
    case SETTING_LOAD:
      return {
        ...state
      };
    case SETTING_LOAD_SUCCESS:
      return {
        ...state
      };
    case SETTING_LOAD_FAILED:
      return {
        ...state
      };
    case SETTING_ADD:
      return {
        ...state
      };
    case SETTING_ADD_SUCCESS:
      return {
        ...state
      };
    case SETTING_ADD_FAILED:
      return {
        ...state
      };
    case SETTING_UPDATE:
      return {
        ...state
      };
    case SETTING_UPDATE_SUCCESS:
      return {
        ...state
      };
    case SETTING_UPDATE_FAILED:
      return {
        ...state
      };
    case SETTING_DELETE:
      return {
        ...state
      };
    case SETTING_DELETE_SUCCESS:
      return {
        ...state,
        message: action.message
      };
    case SETTING_DELETE_FAILED:
      return {
        ...state
      };
    case SETTING_DELETE_ALL:
      return {
        ...state
      };
    case SETTING_DELETE_ALL_SUCCESS:
      return {
        ...state,
        message: action.message
      };
    case SETTING_DELETE_ALL_FAILED:
      return {
        ...state
      };
    default:
      return state;
  }
}
